import React from 'react'
import { fetchISSloc , fetchAstro} from './API/fetchData'
import Maps from './components/Maps'
import Astro from './components/Astro'
import styles from './App.module.css'
import image from './Images/251640.jpg'
import About from './components/About'
import ScrollIntoView from 'react-scroll-into-view'
class App extends React.Component
{
    state = {
        Lat : "",
        Lan : "",
        flag : false,
        astronauts : [],
        astroflag : false,
    }

    componentDidMount()
    {
        this.getIssLoc()
        this.getAstro()
    }

    getIssLoc = async() => {
        const response = await fetchISSloc.get()
      //  console.log(response.data.iss_position) 
        this.setState({
                Lat : response.data.iss_position.latitude,
                Lan : response.data.iss_position.longitude,
                flag : true
            })
    }

     getAstro = async() => {
         const response = await fetchAstro.get()
         this.setState({
             astronauts : response.data,
             astroflag : true
         })
     }

    render()
    {
        if(this.state.flag && this.state.astroflag){
        return(<div>
            <img style ={{ zIndex : '-4' , width : '100%'}}src={image} alt = 'ISS'/>
            <h1 style={{color : 'white' , marginTop : '-1200px' , textDecoration : 'underline'}}><center> International Space Station </center></h1>
          <div className = {styles.scrollview}>
          <ScrollIntoView selector='#maps'>
                <h2 style={{color : 'white'}}>Current Position of ISS</h2>
              </ScrollIntoView>
              <ScrollIntoView selector='#astro'>
                <h2 style={{color : 'white' , marginLeft : '50px'}}>Astronauts on ISS</h2>
              </ScrollIntoView>
              <ScrollIntoView selector='#about'>
                <h2 style={{color : 'white' , marginLeft : '50px'}}>About ISS</h2>
              </ScrollIntoView>
              </div>
              <div id='maps'>
           <Maps Lat = {this.state.Lat} Lan = {this.state.Lan}/>
           </div>
           <div id="astro" className = {styles.astro_div}> <Astro astronauts = {this.state.astronauts}/> </div>
           <div id="about"><About /></div>
            </div>)
    }return(
        <div style = {{color : 'white' , marginTop : '25%'}}><center><h1>Loading...</h1></center></div>)
    }
}

export default App