import axios from 'axios'
export const fetchISSloc = axios.create({
    method : 'get',
    url : 'https://cors-anywhere.herokuapp.com/http://api.open-notify.org/iss-now.json',
    headers : { 'Origin' : 'http://api.open-notify.org/iss-now.json'}
})

export const fetchISSPassTime = axios.create({
    method : 'get',
    url : 'https://cors-anywhere.herokuapp.com/http://api.open-notify.org/iss-pass.json',
    headers : { 'Origin' : 'http://api.open-notify.org/iss-pass.json'}
})

export const fetchAstro = axios.create(
    {
        method : 'get',
        url : 'https://cors-anywhere.herokuapp.com/http://api.open-notify.org/astros.json',
        headers: { 'Origin' : 'http://api.open-notify.org/astros.json'}
    }
)
