import React from 'react';
import mapboxgl from 'mapbox-gl';
import styles from './site.module.css'
mapboxgl.accessToken = 'pk.eyJ1IjoiYmh1c2g5OCIsImEiOiJja2NkZ2k0aTEwZWVpMnBtbHF1a3VodzF1In0.oXwbCZ9qRR4nQw_cT4NBqQ';

class Maps extends React.Component {

  componentDidMount() {
    //  console.log(`Came from App js value of Lat and Lan ${this.props.Lat} , ${this.props.Lan}`)
      const Lat = this.props.Lat
      const Lan = this.props.Lan
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/mapbox/streets-v11',
      center: [Lan, Lat],
      zoom: 3
    });

    //console.log(Lan , Lat)

    map.on('load', function() {
       // console.log(Lan , Lat)
        const lat = Lat
        const lan = Lan
        map.loadImage(
       'https://cdn1.iconfinder.com/data/icons/space-butterscotch-vol-2/256/Orbital_Station-512.png',
        function(error, image) {
         //   console.log(`This is in image function ${lat} , ${lan}`)
        if (error) throw error;
        map.addImage('cat', image);
        map.addSource('point', {
        'type': 'geojson',
        'data': {
        'type': 'FeatureCollection',
        'features': [
        {
        'type': 'Feature',
        'geometry': {
        'type': 'Point',
        'coordinates': [lan,lat]
        }
        }
        ]
        }
        });
        map.addLayer({
        'id': 'points',
        'type': 'symbol',
        'source': 'point',
        'layout': {
        'icon-image': 'cat',
        'icon-size': 0.1
        }
        });
        }
        );
        })
  }

  render() {
    return (
      <div className = {styles.map}>
        <h2 className = {styles.map_header} style={{ color : 'white' , marginTop : '67%'}}><center>Current Position of ISS</center></h2>
        <div ref={el => this.mapContainer = el} className={styles.mapContainer} />
      </div>
    )
  }
}

export default Maps
