import React from 'react'
import Card from '@material-ui/core/Card'
import Grid from '@material-ui/core/Grid'
import CardContent from '@material-ui/core/CardContent'
import { Typography } from '@material-ui/core'
import styles from '../App.module.css'
import image from '../Images/astro.png'
const Astro = props => {
    const astro = props.astronauts.people.map(astro => {
        return(
            <Grid item xs={3} sm={2} key ={astro.name}>
                <Card className = {styles.card_div}>
                    <img className = {styles.img_container} src={image} alt='astronaut' />
                    <CardContent>
                    <Typography>
                        {astro.name}
                        </Typography>
                        </CardContent>
                    </Card>
                    </Grid>
            )
    })
    return (
        <div>
        <h2 className={styles.header}> Astronauts on ISS </h2>
        <Grid container className = {styles.container_div}spacing = {0.3}>
            {astro}
            </Grid>
            </div>
    )
}

export default Astro