import React from 'react'

const About = () => {
    return(
        <div style = {{ color : 'white'}}>
            <h2 style = {{ textAlign : 'center'}}>About</h2>
            <p style = {{color : 'white' , marginTop : '3%' , margin : '2%' , fontSize : '20px'}}>
            The International Space Station (ISS) is a modular space station (habitable artificial satellite) in low Earth orbit. The ISS program is a multi-national collaborative project between five participating space agencies: NASA (United States), Roscosmos (Russia), JAXA (Japan), ESA (Europe), and CSA (Canada).[6][7] It is an international collaborative effort between multiple countries. The ownership and use of the space station is established by intergovernmental treaties and agreements.[8] It evolved from the Space Station Freedom proposal.

The ISS serves as a microgravity and space environment research laboratory in which scientific experiments are conducted in astrobiology, astronomy, meteorology, physics, and other fields.[9][10][11] The station is suited for testing the spacecraft systems and equipment required for possible future long-duration missions to the Moon and Mars.[12] It is the largest artificial object in space and the largest satellite in low Earth orbit, regularly visible to the naked eye from Earth's surface.[13][14] It maintains an orbit with an average altitude of 400 kilometres (250 mi) by means of reboost manoeuvres using the engines of the Zvezda Service Module or visiting spacecraft.[15] The ISS circles the Earth in roughly 93 minutes, completing 15.5 orbits per day.
<a style = {{color : 'white'}} href="https://en.wikipedia.org/wiki/International_Space_Station">  ISS on Wikipedia</a>
                </p>
            </div>
    )
}

export default About    